
import { defineComponent, computed, watch, ComputedRef, ref, nextTick, onBeforeUpdate ,PropType } from 'vue'
import { HlCheckbox } from 'hongluan-ui'
import type { IDropdownCheckbox } from './use-dropdown-checkbox'

export default defineComponent({
  name: 'DropdownCheckboxItem',
  components:{ HlCheckbox },
  props:{
    modelValue: {
      type: Array as PropType<IDropdownCheckbox[]>,
      required: true,
      default: () => [],
    },
    ind: {
      required: false,
      type: [Number, String],
      default: 0,
    },
  },
  emits: ['selected-value'],
  setup(props, { emit }){
    const data = ref([])
    const itemList = ref([])

    const pageData: ComputedRef<{[propsName: string]: any;}> = computed(() => (props.modelValue === null || props.modelValue === undefined) ? [] : props.modelValue)

    watch(
      () => pageData.value,
      (currentValue: IDropdownCheckbox[]) => {
        data.value = currentValue
      },
      { immediate: true, deep: true },
    )

    const selectedValue = () => {
      emit('selected-value',
        data.value.reduce((total, item) => total && item.checked, 1),
      )
    }
    const getSelectedValue = (val: boolean, item: IDropdownCheckbox) => {
      if (item.checked !== val) {
        item.checked = val
        selectedValue()
      }
    }
    const selectSubset = (item: IDropdownCheckbox, index: number) => {
      if (item.children && item.children.length > 0) {
        nextTick(() => {
          itemList.value[`item${index}`].setAllCheck(item.checked)
        })
      }
      selectedValue()
    }
    const setAllCheck = (val: boolean) => {
      data.value.forEach((item, index) => {
        item.checked = val
        if (item.children && item.children.length > 0) {
          nextTick(() => {
            itemList.value[`item${index}`].setAllCheck(val)
          })
        }
      })
    }
    const getAllCheck = () => {
      return data.value.reduce((total, item, index) => {
        if (item.children && item.children.length > 0) {
          total = total.concat( itemList.value[`item${index}`].getAllCheck())
        } else if (item.checked) {
          total.push(item.code)
        }
        return total
      }, [])
    }

    onBeforeUpdate(() => itemList.value = [])

    return {
      pageData: data.value,
      itemList,

      setAllCheck,
      getAllCheck,

      getSelectedValue,
      selectSubset,
    }
  },
})
