      type: String,
      default: '',
    },
    close: {
      type: Boolean,
      default: true,
    },
    action: {
      type: Boolean,
      default: true,
    },
  },
  emits: ['close'],
  setup() {
    const { prefix } = usePrefix()
    return {
      prefix,
    }
  },
})
