
import { defineComponent, ref } from 'vue'
import DropdownCheckbox from '../../../../../packages/dropdown-checkbox/index'

const pointData = [
  { name: '', code: 'POINT_TYPE_1', checked: true },
  { name: '', code: 'POINT_TYPE_2', checked: true },
  { name: '', code: 'POINT_TYPE_3', checked: true },
  { name: '', code: 'POINT_TYPE_4', checked: true },
]
const AreaData = require('./area.json')

export default defineComponent({
  name: 'DropdownCheckbox',
  group: '',
  components: {
    'ho-dropdown-checkbox': DropdownCheckbox,
  },
  setup() {
    const dictPointType = ref(pointData)

    AreaData.data.map(item => {
      item.showChild = false
      if (item.children && item.children.length > 0) {
        item.children.map(it => {
          it.showChild = false
        })
      }
    })
    const worksitecodes = ref(AreaData.data)

    const postConfirmEvent = (val: string[]) => {
      console.log(val, 'on-confirm')
    }
    const postShowDownList = (val:string) => {
      console.log(val, 'on-show-list')
    }
    return {
      dictPointType,
      worksitecodes,
      postConfirmEvent,
      postShowDownList,
    }
  },
})
