import { App } from 'vue'
import type { SFCWithInstall } from '../utils/types'
import { PREFIX } from '../utils/constants'
import TitleIndicator from './src/title-indicator.vue'

TitleIndicator.install = (app: App): void => {
  app.component(PREFIX + TitleIndicator.name, TitleIndicator)
}

const _TitleIndicator = TitleIndicator as SFCWithInstall<typeof TitleIndicator>

export default _TitleIndicator
