<template>
  <div :class="prefix + '-dialog-wrapper'">
    <div :class="[`${prefix}-dialog ${prefix}-dialog-animation`, size]">
      <div class="card-bg">
        <div class="card-bg-top"></div>
        <div class="card-bg-middle"></div>
        <div class="card-bg-bottom"></div>
      </div>
      <div class="card-title">
        <slot name="title-icon">
        </slot>
        <div class="title-txt">{{ title }}</div>
        <div
          class="close-icon"
          @click.stop.prevent="$emit('close')"
        ></div>
      </div>
      <slot name="body">
      </slot>
    </div>
  </div>
</template>

<script lang="ts">
import { usePrefix } from '../../utils/util'
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'Dialog',
  props: {
    title: {
      type: String,
      default: '',
    },
    size: {
      type: String,
      default: '',
    },
  },
  emits: ['close'],
  setup() {
    const { prefix } = usePrefix()
    return {  prefix }
  },
})
</script>

