<template>
  <div :class="prefix + '-card-list'" :style="cardListStyle">
    <div
      v-for="(item, index) in pageData"
      :key="index + 'index'"
      class="card-wrap"
    >
      <div v-if="item.checked" class="top-checked-line"></div>
      <div v-if="item.checked" class="bottom-checked-line"></div>
      <div v-if="item.checked" class="checked-icon"></div>
      <div
        :class="{'card': true, 'checked-card': item.checked, 'disabled-card': item.disabled}"
        :style="cardStyle"
        @click="handleClick(index, item)"
      >
        <div :class="[showSlider ? 'slide-up': 'no-slide-up']">
          <div class="floor-box">
            <slot name="box" :data="item"></slot>
          </div>
          <div v-if="showSlider" class="box" @click="handleDetail(item)">
            <slot name="slider" :data="item"></slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script lang="ts">
import { computed, watch, ref, defineComponent, PropType } from 'vue'
import { usePrefix } from '../../utils/util'

interface CardDataType {
  checked: boolean
  disabled: boolean
}

export default defineComponent({
  name: 'CardList',
  props: {
    cardData: {
      required: true,
      type: Array as PropType<CardDataType[]>,
    },
    gap: {
      type: Array,
      default: () => ['0.9rem', '0.9rem'],
    },
    cardWidth: {
      type: String,
      default: '9.5rem',
    },
    cardHeight: {
      type: String,
      default: '5.8rem',
    },
    minHeight: {
      type: String,
      default: '5.8rem',
    },
    showSlider: {
      type: Boolean,
      default: true,
    },
  },
  emits: ['on-detail', 'on-click'],
  setup(props, { emit }) {
    const pageData = ref<CardDataType[]>([])
    const { prefix } = usePrefix()
    const cardListStyle = computed(() => {
      return `
        grid-template-columns: repeat(auto-fill, minmax(${props.cardWidth || '9.5rem'}, 1fr));
        grid-row-gap: ${props.gap[0] || '0.9rem'};
        grid-column-gap: ${props.gap[1] || '0.9rem'};
      `
    })

    const cardStyle = computed(() => {
      return `
        height: ${props.cardHeight || '5.8rem'};
        minHeight: ${props.minHeight || '5.8rem'};
      `
    })

    watch(() => props.cardData, (newVal: CardDataType[]) => {
      pageData.value.splice(0, pageData.value.length)
      pageData.value.push(...newVal)
    }, {
      deep: true,
      immediate: true,
    })

    const handleDetail = item => {
      emit('on-detail', item, pageData.value)
    }

    const handleClick = (index: number, item: CardDataType) => {
      if (item.disabled) {
        return
      }
      pageData.value[index].checked = !item.checked
      emit('on-click', item, pageData.value)
    }

    return {
      prefix,
      pageData,
      cardListStyle,
      cardStyle,
      handleDetail,
      handleClick,
    }
  },
})
</script>
