
import { usePrefix } from '../../utils/util'
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'PicContainer',
  props: {
    data: {
      type: Array,
      default: () => [],
    },
    curIndex: {
      type: Number,
      default: 0,
    },
    animateName: {
      type: String,
      default: 'fadeOwner',
    },
  },
  emits: ['close', 'on-click'],
  setup(props, { emit }) {
    const { prefix } = usePrefix()
    const imgClick = item => {
      emit('on-click', item)
    }
    return { prefix, imgClick }
  },
})
