import { ref, getCurrentInstance, watchEffect } from 'vue'
export interface IDropdownCheckbox {
  name: string
  code: string
  checked: boolean
  showChild?: boolean
  children?: Array<IDropdownCheckbox>
}

export const useShowDropdownBox = () => {
  const { emit } = getCurrentInstance()
  const showDropdownContent = ref(false)

  const showDownList = () => {
    showDropdownContent.value = !showDropdownContent.value
    showDropdownContent.value && emit('on-show-list')
  }
  return { showDropdownContent, showDownList }
}

export const useDropdownContent = (showDropdownContent: {value: boolean;}, pageData) => {
  const { emit } = getCurrentInstance()
  const checkAll = ref(false)
  const componentItem = ref()

  // watch pageData,checkAll
  watchEffect(() => {
    checkAll.value = pageData.reduce((total: boolean, item: IDropdownCheckbox) => total && item.checked, 1)
  })

  const selectAll = () => {
    componentItem.value.setAllCheck(checkAll.value)
  }
  const recursiveData = (data: IDropdownCheckbox[], checkedTypeDataEvent: string[]) =>{
    data.filter(item => {
      if (item.children && item.children.length > 0) {
        recursiveData(item.children, checkedTypeDataEvent)
        const arr = item.children.filter(subItem => subItem.checked)
        item.checked = arr.length === item.children.length
      } else {
        item.checked = checkedTypeDataEvent.includes(item.code)
      }
    })
  }
  const confirm = () => {
    showDropdownContent.value = false
    recursiveData(pageData, componentItem.value.getAllCheck())
    emit('on-confirm', checkAll.value? [] : componentItem.value.getAllCheck()) //  []
  }
  return { checkAll, selectAll, confirm, componentItem  }
}
