
import { defineComponent } from 'vue'
import { usePrefix } from '../../../utils/util'
import ModalBox from './box.vue'

export default defineComponent({
  name: 'ModalPopbox',
  components: { ModalBox },
  props: {
    title: {
      type: String,
      default: '',
    },
    showTitle: {
      type: Boolean,
      default: true,
    },
    close: {
      type: Boolean,
      default: true,
    },
    boxClass: {
      type: String,
      default: '',
    },
    mask: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['close'],
  setup() {
    const { prefix } = usePrefix()
    return {
      prefix,
    }
  },
})
