<template>
  <div
    :class="prefix + '-title-indicator'"
    style="cursor: pointer;"
    @click.stop.prevent="$emit('on-click')"
  >
    <span class="title-txt">{{ title }}</span>
  </div>
</template>
<script>
import { defineComponent } from 'vue'
import { usePrefix } from '../../utils/util'

export default defineComponent({
  name: 'TitleIndicator',
  props: {
    title: {
      type: String,
      default: '',
    },
  },
  emits: ['on-click'],
  setup() {
    const { prefix } = usePrefix()

    return { prefix }
  },
})
</script>
