<template>
  <div
    ref="root"
    :class="[prefix + '-dropdown-checkbox', showDropdownContent? 'is-focus' : '']"
  >
    <div
      class="dropdown-checkbox-title"
      @click.stop="showDownList()"
    >
      <span class="title-txt">{{ title }}</span>
      <i
        class="system-icon arrow-down"
        :class="{'icon-up': showDropdownContent}"
      ></i>
    </div>
    <div v-if="showDropdownContent" class="dropdown-checkbox-content">
      <hl-checkbox
        v-model="checkAll"
        size="sm"
        class="check-all"
        @change="selectAll()"
      >
        <p class="item-name">
          
        </p>
      </hl-checkbox>
      <dropdown-checkbox-item
        ref="componentItem"
        v-model="pageData"
        @selected-value="val => (checkAll = val)"
      />
      <hl-button class="btn-line" type="primary" size="sm" @click.stop="confirm"><span></span></hl-button>
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent, computed, ComputedRef, PropType, onMounted, onBeforeUnmount, ref } from 'vue'
import { HlButton, HlCheckbox } from 'hongluan-ui'
import { usePrefix } from '../../utils/util'
import { useShowDropdownBox, useDropdownContent, IDropdownCheckbox } from './use-dropdown-checkbox'
import DropdownCheckboxItem from './dropdown-checkbox-item.vue'

export default defineComponent({
  name: 'DropdownCheckbox',
  components:{ DropdownCheckboxItem, HlButton, HlCheckbox },
  props:{
    modelValue: {
      type: Array as PropType<IDropdownCheckbox[]>,
      required: true,
      default: () => [],
    },
    title: {
      required: true,
      type: String,
      default:'',
    },
  },
  emits: ['on-show-list', 'on-confirm'],
  setup(props){
    const { prefix } = usePrefix()
    const root = ref(null)

    const pageData: ComputedRef<{[propsName:string]: any;}> = computed(() => (props.modelValue === null || props.modelValue === undefined) ? [] : props.modelValue)

    const { showDropdownContent, showDownList } = useShowDropdownBox()
    const hideDropdownContent = (e: Event) => {
      if (!root.value.contains(e.target)) showDropdownContent.value = false
    }

    onMounted(() => {
      document.addEventListener('click', hideDropdownContent)
    })

    onBeforeUnmount(() => {
      if (root.value) document.removeEventListener('click', hideDropdownContent)
    })

    return {
      root,
      prefix,
      showDropdownContent,
      pageData,
      showDownList,
      ...useDropdownContent(showDropdownContent, pageData.value),
    }
  },
})
</script>
