<template>
  <div :class="[prefix + '-ranking-list', type]">
    <table>
      <thead v-if="hasHeader">
        <tr>
          <th :class="['index', indexConfig.class]" :data-index="indexConfig.label"></th>
          <th v-for="(item, num) of cols" :key="num" :style="{width: item.width? item.width:'auto'}" :align="item.align?item.align:'left'">{{ item.title }}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) of data" :key="index" :class="['bg-' + item.background]" @click="$emit('on-row-click', item, index)">
          <td
            :class="['index', 'top' + (index > 3 ? 'other' : index + 1), indexConfig.class]"
            :data-index="indexConfig.generator ? indexConfig.generator(item, index) : (type === 'type3' || type === 'type4' ? 'Top' : '') + (index + 1)"
          ></td>
          <td v-for="(val, num) of cols" :key="num" :class="{'is-nowrap': val.isNowrap}" :style="{width: val.width? val.width:'auto'}" :align="val.align">
            <template v-if="!val.slotName">{{ item[val.prop] }}</template>
            <template v-else>
              <slot :name="val.slotName" :row="item"></slot>
            </template>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script lang="ts">
import { defineComponent } from 'vue'
import { usePrefix } from '../../utils/util'

export default defineComponent({
  name: 'RankingList',
  props: {
    data: {
      type: Array,
      default: () => [],
    },
    hasHeader: {
      type: Boolean,
      default: false,
    },
    cols: {
      type: Array,
      default: () => [],
    },
    type: {
      type: String,
      default: '',
    },
    indexConfig: {
      type: Object,
      default: () => ({ label: '' }),
    },
  },
  emits: ['on-row-click'],
  setup() {
    const { prefix } = usePrefix()

    return { prefix }
  },
})
</script>

