<template>
  <div
    :class="[prefix + '-special-tab', { [prefix + '-special-tab-line']: line }]"
  >
    <div
      v-for="(item, index) in data"
      :key="index"
      :class="[tabClass(item, index), 'tab', { 'tab-nofirst': index > 0 }]"
      @click="tabClick(index)"
    >
      <span :class="{ 'tab-txt': opacity != 1 }">{{ item.label }}</span>
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent, PropType, ref } from 'vue'
import { usePrefix } from '../../utils/util'

export default defineComponent({
  name: 'SpecialTab',
  props: {
    data: {
      type: Array as PropType<{ label: string; size?: string; }[]>,
      default: () => [],
    },
    opacity: {
      type: Number,
      default: 1,
    },
    line: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['on-change'],
  setup(props, { emit }) {
    const { prefix } = usePrefix()
    const seletedIndex = ref(0)
    const tabClick = index => {
      seletedIndex.value = index
      emit('on-change', index)
    }
    const tabClass = (
      item: { label: string; size?: string; },
      index: number,
    ) => {
      let className = ''
      if (index === 0) {
        className = seletedIndex.value == index ? `tab01` : `tab00`
      } else {
        className =
          item.size === 'big'
            ? seletedIndex.value == index ? `tab21` : `tab20`
            : seletedIndex.value == index ? `tab11` : `tab10`
      }
      return className
    }
    return {
      prefix,
      seletedIndex,
      tabClick,
      tabClass,
    }
  },
})
</script>
