export interface ComponentsItem {
  name: string
  components: { [propsName: string]: any; }
  group?: string
  hide?: boolean
}


let components: Array<ComponentsItem> = []
const result: Array<ComponentsItem> = []

const componentContext = require.context('.', true, /\.vue$/)
componentContext.keys().forEach(component => {
  const componentModule = componentContext(component)
  /**
   *  import export  require module.export 
   */
  components = [...components, (componentModule.default || componentModule)]
})

components.sort((c1, c2) => (c1.name).localeCompare(c2.name))

result.push(...components)

export  const DemoComponents = result
