import { PREFIX } from './constants'

export function usePrefix() {
  return { PREFIX, prefix: PREFIX.toLocaleLowerCase() }
}
export function throttle<T extends (...args: any) => any>(fn: T, delay = 100) {
  let timer = null

  return function (...rest) {
    if (timer) {
      return
    }
    timer = setTimeout(() => {
      fn.apply(this, rest)
      timer = null
    }, delay)
  }
}
