<template>
  <div v-if="mask" :class="prefix+'-modal-popbox-wrapper'">
    <ModalBox
      :title="title"
      :show-title="showTitle"
      :close="close"
      :class="boxClass"
      @close="$emit('close')"
    >
      <template #title>
        <slot name="title">
        </slot>
      </template>
      <template #body>
        <slot name="body">
        </slot>
      </template>
    </ModalBox>
  </div>
  <ModalBox
    v-else
    :title="title"
    :show-title="showTitle"
    :close="close"
    :class="boxClass"
  >
    <template #title>
      <slot name="title">
      </slot>
    </template>
    <template #body>
      <slot name="body">
      </slot>
    </template>
  </ModalBox>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { usePrefix } from '../../../utils/util'
import ModalBox from './box.vue'

export default defineComponent({
  name: 'ModalPopbox',
  components: { ModalBox },
  props: {
    title: {
      type: String,
      default: '',
    },
    showTitle: {
      type: Boolean,
      default: true,
    },
    close: {
      type: Boolean,
      default: true,
    },
    boxClass: {
      type: String,
      default: '',
    },
    mask: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['close'],
  setup() {
    const { prefix } = usePrefix()
    return {
      prefix,
    }
  },
})
</script>
