<template>
  <div
    v-for="(item, index) in pageData"
    :key="index + 'item'"
    class="dropdown-checkbox-item"
  >
    <div class="dropdown-checkbox-item-content">
      <hl-checkbox
        v-model="item.checked"
        size="sm"
        @change="selectSubset(item, index)"
      />
      <p
        class="item-name"
        :class="{'is-cursor':item.children && item.children.length > 0}"
        @click.stop="item.showChild = !item.showChild"
      >
        {{ item.name }}
      </p>
      <i
        v-if="item.children && item.children.length > 0"
        class="system-icon arrow-down"
        :class="[item.showChild ? 'icon-up' : '']"
        @click.stop="item.showChild = !item.showChild"
      ></i>
    </div>
    <!--  -->
    <div
      v-show="item.showChild && item.children && item.children.length > 0"
      :style="[ind === 0 ? `padding-left:${10}px` : `padding-left:${ind * 10}px`]"
    >
      <dropdown-checkbox-item
        :ref="el => itemList['item' + index] = el "
        v-model="item.children"
        :ind="ind + 1"
        @selected-value="val => getSelectedValue(val, item)"
      />
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, watch, ComputedRef, ref, nextTick, onBeforeUpdate ,PropType } from 'vue'
import { HlCheckbox } from 'hongluan-ui'
import type { IDropdownCheckbox } from './use-dropdown-checkbox'

export default defineComponent({
  name: 'DropdownCheckboxItem',
  components:{ HlCheckbox },
  props:{
    modelValue: {
      type: Array as PropType<IDropdownCheckbox[]>,
      required: true,
      default: () => [],
    },
    ind: {
      required: false,
      type: [Number, String],
      default: 0,
    },
  },
  emits: ['selected-value'],
  setup(props, { emit }){
    const data = ref([])
    const itemList = ref([])

    const pageData: ComputedRef<{[propsName: string]: any;}> = computed(() => (props.modelValue === null || props.modelValue === undefined) ? [] : props.modelValue)

    watch(
      () => pageData.value,
      (currentValue: IDropdownCheckbox[]) => {
        data.value = currentValue
      },
      { immediate: true, deep: true },
    )

    const selectedValue = () => {
      emit('selected-value',
        data.value.reduce((total, item) => total && item.checked, 1),
      )
    }
    const getSelectedValue = (val: boolean, item: IDropdownCheckbox) => {
      if (item.checked !== val) {
        item.checked = val
        selectedValue()
      }
    }
    const selectSubset = (item: IDropdownCheckbox, index: number) => {
      if (item.children && item.children.length > 0) {
        nextTick(() => {
          itemList.value[`item${index}`].setAllCheck(item.checked)
        })
      }
      selectedValue()
    }
    const setAllCheck = (val: boolean) => {
      data.value.forEach((item, index) => {
        item.checked = val
        if (item.children && item.children.length > 0) {
          nextTick(() => {
            itemList.value[`item${index}`].setAllCheck(val)
          })
        }
      })
    }
    const getAllCheck = () => {
      return data.value.reduce((total, item, index) => {
        if (item.children && item.children.length > 0) {
          total = total.concat( itemList.value[`item${index}`].getAllCheck())
        } else if (item.checked) {
          total.push(item.code)
        }
        return total
      }, [])
    }

    onBeforeUpdate(() => itemList.value = [])

    return {
      pageData: data.value,
      itemList,

      setAllCheck,
      getAllCheck,

      getSelectedValue,
      selectSubset,
    }
  },
})
</script>

