<template>
  <div class="markdown-code">
    <!-- <div class="box-divider">
      <span class="txt" @click="showDoc = !showDoc">
        {{ type === 'md' ? 'API' : 'Style' }} Document&nbsp;&nbsp;
        <hl-icon name="ChevronsRight" size="xxs" class="m-r-lg" />
      </span>
    </div> -->
    <div v-if="showDoc" class="card-box">
      <div class="markdown-content" v-html="code"></div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'Markdown',
  components: {},
  props: {
    type: {
      type: String,
      default: 'md',
    },
    path: {
      type: String,
      default: 'button',
    },
  },
  setup(props){
    const showDoc = ref(true)
    const code = ref()

    if (props.type === 'md') {
      import('@docs/' + props.path + '/README.md').then(resp => {
        code.value = resp.default
      })
    } else {
      // import('!!raw-loader!@style/' + props.path).then(resp => {
      //   code.value = '<pre><code class="language-css">' + resp.default + '</code></pre>';
      // });
    }
    return {
      showDoc,
      code,
    }
  },
})
</script>
