<template>
  <div :class="prefix + '-special-tab2'" :style="changeStyle">
    <div class="mtabs">
      <div
        v-for="(item, index) in data"
        :key="index"
        :class="[
          'tab',
          { threepart: data.length >= 3 },
          { twopart: data.length === 2 }
        ]"
        @mouseover="liMouseOver(index)"
        @mouseleave="dealLeave"
        @click="liClick(index)"
      >
        <div :class="['tab_bg','tab_bg' +getTabImg(index)]"></div>
        <span
          v-if="btnSelectedIndex == index"
          v-cloak
          class="tab-name select-color"
        >{{ item }}</span>
        <span
          v-else-if="btnHoveredIndex == index"
          v-cloak
          class="tab-name hover-color"
        >{{ item }}</span>
        <span v-else v-cloak class="tab-name init-color">{{ item }}</span>
      </div>
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent, PropType, ref } from 'vue'
import { usePrefix,throttle } from '../../utils/util'
export default defineComponent({
  // 2-3tab
  name: 'SpecialTab2',
  props: {
    data: {
      type: Array as PropType<string[]>,
      default: () => [],
    },
    changeStyle: {
      type: Object,
      default: () => ({}),
    },
  },
  emits: ['on-change'],
  setup(props,{ emit }) {
    const { prefix } = usePrefix()
    const btnSelectedIndex = ref(0)
    const btnHoveredIndex = ref(-1)
    const changeIndex = index => {
      emit('on-change', index, props.data[index])
    }
    const liMouseOver = index => {
      btnHoveredIndex.value = index
    }
    const liClick = index => {
      btnSelectedIndex.value = index
      btnHoveredIndex.value = -1
      changeIndex(index)
    }
    const hideListThrottle = () =>{
      btnHoveredIndex.value = -1
    }
    const getTabImg = index =>{
      const prefix = btnSelectedIndex.value === index ? '10' : '00'
      return prefix + (index === 0 ? '0' : (index + 1 === (props.data as string[]).length ? '2' : '1'))
    }
    const dealLeave = throttle(hideListThrottle,100)

    return {
      prefix,
      btnSelectedIndex,
      btnHoveredIndex,
      liMouseOver,
      liClick,
      hideListThrottle,
      getTabImg,
      dealLeave,
    }
  },
})
</script>
