
import { defineComponent, onMounted, ref, shallowRef } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { DemoComponents } from './demos/index'
import Readme from './components/readme.vue'
import ChangedHistory from './components/changed.vue'

enum  MenuStatus {
  README =  '-1',
  HISTORY = '-2',
}

const useGetMenu = () => {
  const noSubmenuArray = [{ key: '', code: '-1' }, { key: '', code: '-2' }]
  const sortIdx = ['', '', '', '', '', '']
  const showAll = document.location.href.indexOf('all=true') !== -1
  const DemoComponentsArr = [], DemoComponentsMap = {}

  DemoComponents.forEach(item => {
    if (!item.group) item.group = ''
    if (!item.hide || showAll) {
      if (!DemoComponentsMap[item.group]) {
        DemoComponentsMap[item.group] = []
      }
      DemoComponentsMap[item.group].push(item)
    }
  })
  Object.keys(DemoComponentsMap).forEach(k => {
    DemoComponentsArr.push({
      key: k,
      components: DemoComponentsMap[k],
      name: '',
    })
  })
  DemoComponentsArr.sort((d1, d2) => sortIdx.indexOf(d1.key) - sortIdx.indexOf(d2.key))
  const hasSubmenuArray = DemoComponentsArr

  return { noSubmenuArray, hasSubmenuArray }
}

export default defineComponent({
  name: 'Demo',

  setup () {
    const router = useRouter()
    const route = useRoute()

    const defaultActiveMenu = ref('-1')
    const rightMenu = ref(null)
    const componentItem = shallowRef(Readme)
    const { noSubmenuArray, hasSubmenuArray } = useGetMenu()

    const openMenu = (key: any = '-1') => {
      if ( key === MenuStatus.README) {
        componentItem.value = Readme
      } else if ( key === MenuStatus.HISTORY) {
        componentItem.value = ChangedHistory
      } else {
        componentItem.value = key
      }
      router.push({
        query: {
          from: key.name || key,
        },
      })
    }

    onMounted(() => {
      const from = route.query.from as string
      const idx = DemoComponents.findIndex(d => d.name === from)
      if (idx !== -1) {
        openMenu(DemoComponents[idx])
        const groupIdx = hasSubmenuArray.findIndex(d => d.key === DemoComponents[idx].group)
        rightMenu.value.open(groupIdx)
        defaultActiveMenu.value = from + '/' + hasSubmenuArray[groupIdx].components.findIndex(c => c.name === from)
      } else {
        openMenu(from)
        defaultActiveMenu.value = from
      }
    })

    return {
      rightMenu,
      defaultActiveMenu,
      componentItem,
      noSubmenuArray,
      hasSubmenuArray,
      openMenu,
    }
  },
})
