<template>
  <div :class="prefix + '-button-tab'">
    <div
      v-for="(item, index) in list"
      :key="index"
      :class="{'tab-item': true,'active-item': bindValue == item.value,type2: type === 'type2',}"
      @click="handleClickTab(item.value)"
    >
      <span class="label-name">
        {{ item.label }}
      </span>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, ref, watch } from 'vue'
import { usePrefix } from '../../utils/util'

interface ListType {
  label: string
  value: string
}
export default defineComponent({
  name: 'ButtonTab',
  props: {
    modelValue: {
      type: String,
      default: '',
    },
    // tab: [ { label: "1", value: "1" }, ... ]
    list: {
      required: true,
      type: Array as PropType<ListType[]>,
    },
    type: {
      type: String,
      default: '',
    },
  },
  emits: ['on-click', 'update:modelValue'],
  setup(props, { emit }) {
    const { prefix } = usePrefix()
    const bindValue = ref('')
    watch(
      () => props.modelValue,
      (newVal:string) => {
        bindValue.value = newVal
        if (newVal !== bindValue.value) {
          handleClickTab(newVal)
        }
      },{
        immediate:true,
        deep: true,
      },
    )
    const handleClickTab = value => {
      emit('update:modelValue', value)
      emit('on-click', value)
    }
    return {
      prefix,
      bindValue,
      handleClickTab,
    }
  },
})
</script>
