import { App } from 'vue'
import type { SFCWithInstall } from '../utils/types'
import { PREFIX } from '../utils/constants'
import DropdownCheckbox from './src/dropdown-checkbox.vue'

DropdownCheckbox.install = (app: App): void => {
  app.component(PREFIX + DropdownCheckbox.name, DropdownCheckbox)
}

const _DropdownCheckbox = DropdownCheckbox as SFCWithInstall<typeof DropdownCheckbox>

export default _DropdownCheckbox



