
import { defineComponent, PropType, ref } from 'vue'
import { usePrefix,throttle } from '../../utils/util'
export default defineComponent({
  // 2-3tab
  name: 'SpecialTab2',
  props: {
    data: {
      type: Array as PropType<string[]>,
      default: () => [],
    },
    changeStyle: {
      type: Object,
      default: () => ({}),
    },
  },
  emits: ['on-change'],
  setup(props,{ emit }) {
    const { prefix } = usePrefix()
    const btnSelectedIndex = ref(0)
    const btnHoveredIndex = ref(-1)
    const changeIndex = index => {
      emit('on-change', index, props.data[index])
    }
    const liMouseOver = index => {
      btnHoveredIndex.value = index
    }
    const liClick = index => {
      btnSelectedIndex.value = index
      btnHoveredIndex.value = -1
      changeIndex(index)
    }
    const hideListThrottle = () =>{
      btnHoveredIndex.value = -1
    }
    const getTabImg = index =>{
      const prefix = btnSelectedIndex.value === index ? '10' : '00'
      return prefix + (index === 0 ? '0' : (index + 1 === (props.data as string[]).length ? '2' : '1'))
    }
    const dealLeave = throttle(hideListThrottle,100)

    return {
      prefix,
      btnSelectedIndex,
      btnHoveredIndex,
      liMouseOver,
      liClick,
      hideListThrottle,
      getTabImg,
      dealLeave,
    }
  },
})
