    <div class="popbox-bg">
      <div class="popbox-bg-top">
        <div class="popbox-bg-top-left"></div>
        <div class="popbox-bg-top-middle"></div>
        <div class="popbox-bg-top-right"></div>
        <div class="popbox-bg-top-light"></div>
        <div v-if="showTitle" class="popbox-bg-top-divider"></div>
        <div v-if="close" class="popbox-bg-top-close" @click="$emit('close')"></div>
      </div>
      <div class="popbox-bg-center">
        <div class="popbox-bg-center-left"></div>
        <div class="popbox-bg-center-middle"></div>
        <div class="popbox-bg-center-right"></div>
      </div>
      <div class="popbox-bg-bottom">
        <div class="popbox-bg-bottom-left"></div>
        <div class="popbox-bg-bottom-middle"></div>
        <div class="popbox-bg-bottom-right"></div>
        <div v-if="!action" class="popbox-bg-bottom-light"></div>
        <div v-else class="popbox-bg-bottom-action"></div>
      </div>
    </div>
    <div class="popbox-content">
      <div v-if="showTitle" class="popbox-title">
        <slot name="title">
          <span class="popbox-title-txt">
            <div class="left-icon"> </div>
            <span class="title">{{ title }}</span>
            <div class="right-icon"></div>
          </span>
        </slot>
        <slot name="right-section"></slot>
      </div>
      <slot name="body">
      </slot>
      <div v-if="action" class="popbox-action">
        <slot name="action">
        </slot>
      </div>
    </div>
  </div>
