<script lang="ts">
import { usePrefix } from '../../../utils/util'
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'LargePopbox',
  props: {
    title: {
      type: String,
      default: '',
    },
    showTitle: {
      type: Boolean,
      default: true,
    },
    close: {
      type: Boolean,
      default: true,
    },
    action: {
      type: Boolean,
      default: true,
    },
    mask: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['close'],
  setup() {
    const { prefix } = usePrefix()
    return {
      prefix,
    }
  },
})
