
import { defineComponent, ref } from 'vue'
import Dialog from '@packages/dialog'

export default defineComponent({
  name: 'Dialog',
  group: '',
  components: { 'ho-dialog': Dialog },
  setup() {
    const showNormalDialg = ref(false)
    const dialogSize = ref('')
    const showDialog = (size: string) => {
      dialogSize.value = size
      showNormalDialg.value = true
    }
    return {
      showNormalDialg,
      dialogSize,
      showDialog,
    }
  },
})
