<template>
  <div :class="prefix + '-pic-container'">
    <transition :name="animateName">
      <div
        v-if="data.length > 0"
        class="img-div"
        @click="imgClick(data[curIndex])"
      >
        <img :src="data[curIndex]['imgUrl']">
      </div>
    </transition>
  </div>
</template>

<script lang="ts">
import { usePrefix } from '../../utils/util'
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'PicContainer',
  props: {
    data: {
      type: Array,
      default: () => [],
    },
    curIndex: {
      type: Number,
      default: 0,
    },
    animateName: {
      type: String,
      default: 'fadeOwner',
    },
  },
  emits: ['close', 'on-click'],
  setup(props, { emit }) {
    const { prefix } = usePrefix()
    const imgClick = item => {
      emit('on-click', item)
    }
    return { prefix, imgClick }
  },
})
</script>

