
import { defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'Markdown',
  components: {},
  props: {
    type: {
      type: String,
      default: 'md',
    },
    path: {
      type: String,
      default: 'button',
    },
  },
  setup(props){
    const showDoc = ref(true)
    const code = ref()

    if (props.type === 'md') {
      import('@docs/' + props.path + '/README.md').then(resp => {
        code.value = resp.default
      })
    } else {
      // import('!!raw-loader!@style/' + props.path).then(resp => {
      //   code.value = '<pre><code class="language-css">' + resp.default + '</code></pre>';
      // });
    }
    return {
      showDoc,
      code,
    }
  },
})
